export default {
	data() {
		return {
			userNumber: '', //账号（手机号）
			userpassword: '' //密码
		};
	},
	methods: {
		// 账号
		userNumChange(usernum) {
			this.userNumber = usernum.detail.value;
		},
		// 密码
		userpasswordChange(userpass) {
			this.userpassword = userpass.detail.value;
		},
		// 登录
		loginChange(loginbtn) {
			console.log(loginbtn);
		}
	}
};
